<script>
import {loadLanguage} from '../../i18n'
import VueCookies from 'vue-cookies'
import { eventBus } from "@/main";
export default {
  props: ["queries"],
  data() {
    return {
      vifaaVyabweni: [
        "a) Godoro urefu futi 3 kwa 6, Upana inch 3 na kuendelea",
        "b) Chandarua cheupe cha pembe nne",
        "c) Sanduku la chuma la kuhifadhia vitu vyake",
        "d) Shuka mbili za light blue",
        "e) Sahani, Kikombe na doo ndogo"
      ],
      documents: [
        "PERMANENT PASSPORT with a validity period not less than 2 years, temporary travel documents ARE NOT ALLOWED.",
        "VALID VISA STATUS (STAMPED AT ARRIVAL IN TANZANIA)",
        "BIRTH CERTIFICATE",
        "STUDENT APPLICATION LETTER TO THE MAHAD (TYPED)",
        "PASSPORT SIZE PHOTO",
