        "ANY OTHER RELEVANT DOCUMENTATION may seem necessary to the immigration authorities"
      ],
      paymentDetails: [
        "Admission form: Tshs. 5,000",
        "Student pass processing fee: Tshs. 100,000",
        "Student pass for students outside East Africa (Above 18 years): USD 250",
        "Student pass for students outside East Africa (Below 18): USD 150"
      ],
      detailsDialog: false,
      language: "en",
      messageDialog: {
        enable: false,
        color: 'error',
        title: '',
        message: ''
      },
      edit: false,
      valid: true,
      overlay: false,
      values: {
        regfee: "",
        fname: "",
        surname: "",
        othername: "",
        // photo: "",
        birthDate: "",
        gender: "",
        maritalStatus: "",
        totalDependents: "",
        nationality: "",
        domicile: "",
        street: "",
        phone: "",
        email: "",
        idtype: "",
        idvalue: "",
        kinname: "",
        kinrelation: "",
        kinjob: "",
        kinphone: "",
        religiousEducation: [],
        secularEducation: [],
        communication: [],
        tazkiyyahSheikh: "",
        tazkiyyahSheikhAddress: "",
        tazkiyyahSheikhPhone: "",
        stream: "",
        fathername: "",
        fatherjob: "",
        fatherphone: "",
        mothername: "",
        motherjob: "",
        motherphone: "",
        idExpireDate: "",
        visaNumber: "",
        passportIssueLocation: "",
        appLetter: "",
        birthCertificate: "",
        schoolCertificate: "",
        passportCopy: "",
        policeClearance: ""
      },
      isForeigner: false,
      loggedin: false
    }
  },
  computed: {
    isMobile() {
      if(screen.width > 760) {
        return false
      }
      return true
    }
  },
  components: {
    "fhir-string": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-string" ),
    "fhir-integer": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-integer" ),
    "fhir-choice": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-choice" ),
    "fhir-reference": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-reference" ),
    "fhir-attachment": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-attachment" ),
    "fhir-date": () => import(/* webpackChunkName: "fhir-main" */ "@/components/fhir/fhir-date" ),
    "ihris-array": () => import(/* webpackChunkName: "fhir-main" */ "@/components/ihris/ihris-array" ),
    "ihris-questionnaire-group": () => import(/* webpackChunkName: "fhir-main" */ "@/components/ihris/ihris-questionnaire-group" ),
  },
  methods: {
    handleMenuItemClick () {
      loadLanguage(this.language)
      localStorage.setItem('activeLocale', this.language)
    },
    save() {
      this.$refs.form.validate();
      if (!this.valid) return;
      this.overlay = true
      this.values.communication = []
      this.values.religiousEducation = []
      this.values.secularEducation = []
      this.populateValues(this.$children)
      console.error(JSON.stringify(this.values.communication, 0, 2));
      let opts = {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        redirect: 'manual',
        body: JSON.stringify(this.values)
      }
      fetch("/site/unAuthenticated/registrationApplication", opts).then((response) => {
        if(response.status !== 200 && response.status !== 201) {
          this.overlay = false
          response.json().then((response) => {
            this.messageDialog.color = "error"
            this.messageDialog.enable = true
            this.messageDialog.title = response.title
            this.messageDialog.message = response.message
          })
        } else {
          response.json().then((response) => {
            if(response.type === "failed") {
              this.overlay = false
              this.messageDialog.color = "error"
              this.messageDialog.enable = true
              this.messageDialog.title = response.title
              this.messageDialog.message = response.message
            } else {
              this.messageDialog.color = "success"
              this.messageDialog.enable = true
              // this.messageDialog.title = "OK"
              this.messageDialog.message = "Hongera, Maombi yako yametumwa kikamilifu. Ofisi itawasiliana nawewe kwa maelezo zaidi"
              setTimeout(() => {
                this.overlay = false
                this.logout()
                this.$store.commit("setMessage", {
                  type: "success",
                  text: "Hongera, Maombi yako yametumwa kikamilifu. Ofisi itawasiliana nawewe kwa maelezo zaidi",
                });
                this.$router.push( {path: "/" } )
              }, 8000);
            }
          }).catch(() => {
            this.overlay = false
            this.messageDialog.color = "error"
            this.messageDialog.enable = true
            this.messageDialog.title = "Error"
            this.messageDialog.message = "Unexpected error has occured"
          })
        }
      }).catch(() => {
        this.overlay = false
        this.messageDialog.color = "error"
        this.messageDialog.enable = true
        this.messageDialog.title = "Error"
        this.messageDialog.message = "Unexpected error has occured"
      })
    },
    populateValues(children) {
      for (let child of children) {
        if(child.qField && child.path) {
          this.values[child.path] = child.value
        }
        if (child.$children) {
          if(child.fieldType === "qGroup") {
            let groupedValues = {}
            this.populateGroupedValues(child.$children, groupedValues)
            this.values[child.path].push(groupedValues)
          } else {
            this.populateValues(child.$children) 
          }
        }
      }
    },
    populateGroupedValues(children, groupedValues) {
      for (let child of children) {
        if(child.qField && child.path) {
          groupedValues[child.path] = child.value
        }
        if (child.$children) {
          this.populateGroupedValues(child.$children, groupedValues)
        }
      }
    },
    cancel() {
      this.logout()
      this.$router.push({path: "/"})
    },
    login() {
      let formData = new URLSearchParams()
      formData.append("username", 'applicant@itsf.or.tz')
      formData.append("password", 'AN{d(X3rjP5vV8KhkHyB^9')
      fetch("/auth/login", {
        method: "POST",
        headers: {"Content-Type": "application/x-www-form-urlencoded"},
        body: formData
      }).then(() => {
        this.loggedin = true
        this.overlay = false
        this.language = 'sw'
        console.log('loggedin');
      })
    },
    logout() {
      if(this.$store.state.idp === 'keycloak') {
        VueCookies.remove('userObj')
        this.$keycloak.logout()
      } else {
        fetch("/auth/logout").then(() => {
          this.$store.commit('logout')
          this.$emit('loggedout')
        })
      }
    }
  },
  created() {
    eventBus.$on("nationality", (value) => {
      let nationality
      if(value) {
        nationality = value.split("#")[1]
      }
      if(nationality !== 'TZ') {
        this.isForeigner = true
      } else {
        this.isForeigner = false
      }
    })
    this.overlay = true
    this.login()
    if(this.isMobile) {
      this.$store.state.minidrawer = true
    }
  },
  beforeDestroy() {
    this.logout()
  },
}
</script>
